import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';
import './Stream.css';
import Footer from './Footer';
import { Helmet } from 'react-helmet';

const Stream = () => {

  const value = useParams().type;

  const [anime, setAnime] = useState({});

  const [anify, setanify] = useState([]);

  const [anifyep, setanifyep] = useState("0")

  const [videos, setvideos] = useState('');

  const [server, setserver] = useState("1");

  const [episode, setepisode] = useState([]);

  const [num, setnum] = useState("1");

  const [select, setselect] = useState("1");

  const [dataanify, setdataanify] = useState("one piece");

  useEffect(() => {
    getanime();
  }, [anifyep]);

  const getanime = async () => {
    await fetch(`https://api.jikan.moe/v4/anime/${value}`)
      .then((res) => {
        return res.json();
      })
      .then((data) => {
        const { title, title_english, title_japanese, synopsis, episodes, type, rank, season, year } = data.data;

        setdataanify(title);

        const detail = {
          title,
          title_english,
          title_japanese,
          synopsis,
          episodes,
          type,
          rank,
          season,
          year
        };
        setAnime(detail);
      });
  };

  useEffect(() => {
    getanify();
  }, [anime, server, anifyep, dataanify]);

  const getanify = async () => {
    await fetch(`https://api.anify.tv/search?type=anime&query=${dataanify}&page=1`)
      .then((res) => {
        return res.json();
      })
      .then((data) => {
        setanify(data.results[0].episodes.data[1].episodes[anifyep].id);
      });
  };

  // making iframe src with replace method
  useEffect(() => {
    Server();
  }, [server, num, anime, anify])
  const Server = () => {
    if (server == "1") {
      var video = `${anime.title}`;
      var video = video.replace(/[:()/]/g, '').replace(/[' ']/g, '-');
      var video = `https://player.ryuk.to?id=${video}-episode-${num}`;
      setvideos(video);
    } if (server == "2") {
      var video = `${anify}`;
      var video = video.replace(/[:()/]/g, '').replace(/[' ']/g, '-');
      var video = `https://player.ryuk.to?id=${video}`;
      setvideos(video);
    } else {
      var video = `${anime.title_english}`;
      var video = video.replace(/[:()/]/g, '').replace(/[' ']/g, '-');
      var video = `https://player.ryuk.to?id=${video}-episode-${num}`;
      setvideos(video);
    }
  }

  // making episodes
  useEffect(() => {
    setepisode(num => {
      const newnum = Array(anime.episodes).fill().map((item, index) => index + 1);
      return [...newnum];
    });
  }, [anime.episodes])

  // Changing episode button state
  const epistate = (e) => {
    setnum(e.target.value);
    setanifyep(e.target.value - 1)
    setselect(e.target.value);
  };

  return (
    <div style={{ backgroundColor: "#181818dd" }}>
      <Helmet>
        <title>Aniature - Watch {`${anime.title}`} for free</title>
        <meta
          name="description"
          content={`Watch ${anime.title} for free in aniature. All episode are free and other anime also free in english subbed`}
        />
        <meta
          name="keywords"
          content={`POkemon, demon slayer, attack on titan, spy x family, naruto, naruto shippuden, english subbed, hindi, download, full episode, ${anime.title},`}
        />
      </Helmet>
      <div className='container text-center pt-4'>
        <div className='d-flex row'>
          <div className='video-conatiner col'>
            <iframe className='anivid' src={videos} frameborder="0" allowFullScreen="true"></iframe>
            <p className='color text-center'>You are watching episode {num} of {anime.title}</p>
            <p className='color text-center' style={{ backgroundColor: "rgba(0, 0, 0, 0.999)", borderRadius: "10px", padding: "2px" }}>If current server doesn't work please try other servers beside. And if any problem occurs please refresh page. Some anime can't play. Enjoy your's anime.</p>
            <div className='server'>
              <button className='epivid color' onClick={() => setserver(1)} style={{ backgroundColor: server == "1" ? 'yellowgreen' : '', color: server == "1" ? 'black' : '' }}>Server 1</button>
              <button className='epivid color' onClick={() => setserver(2)} style={{ backgroundColor: server == "2" ? 'yellowgreen' : '', color: server == "2" ? 'black' : '' }}>Server 2</button>
              <button className='epivid color' onClick={() => setserver(3)} style={{ backgroundColor: server == "3" ? 'yellowgreen' : '', color: server == "3" ? 'black' : '' }}>Server 3</button>
            </div>
            <h5 className='text-light d-flex m-3'>All Episodes :</h5>
            {
              episode.map((ele) => {
                return (
                  <div className='epilist' key={ele}>
                    <button onClick={epistate} value={ele} className='epivid color' style={{ backgroundColor: ele == select ? "yellowgreen" : "", color: ele == select ? "black" : "" }}>{ele}</button>
                  </div>
                )
              })
            }
            <p className='color mt-4'>{anime.synopsis}</p>
          </div>

          <div className='video-detail col'>
            <p className='color'>Title: <span className='detail text-light'>{anime.title}</span></p>
            <p className='color'>Title japanese: <span className='detail text-light'>{anime.title_japanese}</span></p>
            <p className='color'>Type: <span className='detail text-light'>{anime.type}</span></p>
            <p className='color'>Episodes: <span className='detail text-light'>{anime.episodes}</span></p>
            <p className='color'>Rank: <span className='detail text-light'>{anime.rank}</span></p>
            <p className='color'>Season: <span className='detail text-light'>{anime.season}</span></p>
            <p className='color'>Year: <span className='detail text-light'>{anime.year}</span></p>
          </div>
        </div>

        <Footer />
      </div>
    </div>
  )
}

export default Stream;
