import React from 'react'
import './Anilist.css';
import { useParams } from 'react-router-dom';
import { useState, useEffect } from 'react';
import Footer from './Footer';
import { Link } from 'react-router-dom';

const Search = (props) => {

  const value = useParams().type;

  const [anime, setAnime] = useState([]);

  useEffect(() => {
    getanime();
  }, [value]);

  const getanime = async () => {
    await fetch(`https://api.jikan.moe/v4/anime?q=${value}`)
      .then((res) => {
        return res.json();
      })
      .then((data) => {
        setAnime(data.data);
        console.log(data.data);
      });
  };

  return (
    <div style={{ backgroundColor: "#181818dd" }}>
      <div className='container-fluid pt-3'>
        <div className='anime-box'>
          <div className='trending d-flex mb-4'>
            <div className='blue bg-primary'></div>
            <h1 className='name text-light'>{value}</h1>
          </div>
          <div className='anime'>
            {anime.map((ele, index) => {
              return (
                <>
                  <div className="card" key={index}>
                    <Link style={{textDecoration:"none"}} to={{
                      pathname: `/stream/${ele.mal_id}`
                    }}><img src={ele.images.jpg.large_image_url} className="card-img-top" alt="loading..." />
                    <div className="card-body">
                      <p className="card-text text-light text-center">{ele.title}</p>
                      <p className="card-text text-light text-center japanese">{ele.title_japanese}</p>
                    </div></Link>
                  </div>
                </>
              )
            })}
          </div>
        </div>

        <Footer />

      </div>
    </div>
  )
}

export default Search;
