import React, { useState } from 'react'
import './Nav.css';
import { Link, Outlet } from 'react-router-dom';
import logo from './logo.png';

const Nav = () => {

    const [search, setsearch] = useState("");

    const inputval = (e) => {
        setsearch(e.target.value);
    }

    return (
        <div>
            <nav className="navbar navbar-expand-lg border-bottom border-body" data-bs-theme="dark">
                <div className="container-fluid">
                    <div className='logos'>
                        <img className='logo' src={logo} alt='' />
                        <a className="navbar-brand text-primary" href="#">ature</a>
                    </div>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                            <li className="nav-item">
                            </li>
                        </ul>
                        <form className="d-flex" role="search">
                            <input className="form-control me-2" type="search" placeholder="Search Anime....." onChange={inputval} aria-label="Search" />
                            <Link to={{
                                pathname: `/search/${search}`
                            }}><button className="btn btn-outline-primary" type="submit">Search</button></Link>
                        </form>
                    </div>
                </div>
            </nav>

            <Outlet />
        </div>
    )
}

export default Nav;
