import React, { useState, useEffect } from 'react'
import './Anilist.css';
import { Link } from 'react-router-dom';

const Anilist = () => {

    const [search, setsearch] = useState("");

    const [anime, setAnime] = useState([]);

    useEffect(() => {
        getanime();
    }, []);

    const getanime = async () => {
        let limit = Math.floor((Math.random() * 21) + 11);
        await fetch(`https://api.jikan.moe/v4/anime?limit=${limit}`)
            .then((res) => {
                return res.json();
            })
            .then((data) => {
                setAnime(data.data);
                console.log(data.data);
            });
    }

    const inputval = (e) => {
        setsearch(e.target.value);
    }

    return (
        <div className='container mt-2'>
            <div>
                <p className='text-center'>If you enjoy the website please consider sharing it with your friends. And some anime can't support our website so they can not play support us i can upload soon that anime, thank you!</p>
                <form className="d-flex justify-content-center" role="search">
                    <input className="form-control me-2 filter" type="search" placeholder=" Search Anime....." onChange={inputval} value={search} aria-label="Search" />
                    <Link to={{
                        pathname: `/search/${search}`
                    }}><button className="btn btn-outline-primary" type="submit">Filter</button></Link>
                </form>
            </div>

            <div className='anime-box mt-4'>
                <div className='trending d-flex'>
                    <div className='blue bg-primary'></div>
                    <h1 className='name text-light'>All Anime</h1>
                </div>
                <div className='anime'>
                    {anime.map((ele) => {
                        return (
                            <>
                                <div className="card mt-3" key={ele.mal_id}>
                                    <Link style={{textDecoration:"none"}} to={{
                                        pathname: `/stream/${ele.mal_id}`
                                    }}><img src={ele.images.jpg.large_image_url} className="card-img-top" alt="loading..." />
                                    <div className="card-body">
                                        <p className="card-text text-light text-center">{ele.title}</p>
                                    </div></Link>
                                </div>
                            </>
                        )
                    })}
                </div>
            </div>

        </div>
    )
}

export default Anilist;
